import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const groupSegmentStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
            }

            ${root} {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }

            ${child('wrapper')} {
                display: flex;
                align-items: var(--group-element-align-items, end);
                width: 100%;
            }
            ${is('row')} ${child('wrapper')} {
                flex-direction: row;
            }
            ${is('column')} ${child('wrapper')} {
                flex-direction: column;
            }

            ${child('label')} {
                display: inline-block;
                text-align: left;
                font-size: 14px;
                margin-bottom: 7px;
                color: ${cssVariableAsVar('color_secondary_7')};
            }

            ${is('row')} {
                --group-element-left-border-compensation: var(--group-element-border-compensation, -1px);
                --group-element-top-border-compensation: 0;
            }
            ${is('column')} {
                --group-element-left-border-compensation: 0;
                --group-element-top-border-compensation: var(--group-element-border-compensation, -1px);
            }

            ::slotted(*) {
                flex: 1;
            }
            ::slotted(:not(:first-child)) {
                margin-left: var(--group-element-left-border-compensation);
                margin-top: var(--group-element-top-border-compensation);
            }
        `,
    ];
};
