import { BaseElement, createClassName, defineElement, html, property } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { groupSegmentStyles } from './groupSegmentStyle';
import { GroupSegmentProperties } from './groupSegmentProperties';

@defineElement({ tagName: 'group-segment', styles: groupSegmentStyles, version })
export class GroupSegment extends BaseElement implements GroupSegmentProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property({ type: String })
    label = '';
    @property({ type: String })
    flow = 'row';

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), { [this.flow]: true });

        return html` <div class="${rootClasses}">
            ${this._renderLabel(baseClassName)}
            <div class="${baseClassName}-wrapper">
                <slot></slot>
            </div>
        </div>`;
    }

    private _renderLabel(baseClassName) {
        return this.label
            ? html`<span class="${baseClassName}-label" title=${this.label}>${this.label}</span>`
            : html``;
    }
}
